// 旨在解决设置消除 inline-block 元素间隙
// 父元素添加 clear-blank 类名，需要去除间隙的子元素添加 clear-blank-item 类名

.clear-blank {
  *word-spacing: -1px;
  font-size: 0;

  & > .clear-blank-item {
    @include display;
    word-spacing: normal;
    font-size: 14px;
  }
}


