@charset "utf-8";
// Blocks
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
dl,
dt,
dd,
ul,
ol,
li,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe {
  margin: 0;
  padding: 0;
}
body,
button,
input,
select,
textarea {
  font: 14px/1.5 '微软雅黑', '宋体', Arial, 'helvetica neue', tahoma, 'hiragino sans gb', stheiti, 'wenquanyi micro hei', \5FAE\8F6F\96C5\9ED1, \5B8B\4F53, sans-serif;
}

// Headings font
h1 {
  font-size: 20px;
}
h2 {
  font-size: 18px;
}
h3 {
  font-size: 16px;
}
h4,
h5,
h6 {
  font-size: 100%;
}

small {
  font-size: 12px;
}

// List
ul,
ol {
  list-style: none;
}

// Form
button,
input,
select,
textarea {
  margin: 0;
  font-size: 100%;
}

// Box sizing
html {
  box-sizing: border-box;
}

* {
  &,
  &:before,
  &:after {
    box-sizing: inherit;
  }
}

// Media
img,
embed,
iframe,
object,
audio,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

// Table
table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

// a
a {
  text-decoration: none;
  color: #333;
  &:hover {
    text-decoration: none;
  }
  &:active,
  &:hover {
    outline: 0;
  }
}

img {
  border: 0;
}

.fl {
  float: left;
}
.fr {
  float: right;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  content: "\200B";
  display: block;
  height: 0;
  clear: both;
}

/* CSS for manage text typography */
.tal {
  text-align: left;
}
.tar {
  text-align: right;
}
.tac {
  text-align: center;
}



