//
// Breadcrumbs
// --------------------------------------------------

.breadcrumb {
  padding: 9px 15px;
  margin: 0 0 $baseMargin;
  list-style: none;
  font-weight: normal;
  @include border-radius($borderRadiusSmall);
  & > li {
    display: inline-block;
    @include ie7-inline-block();
    // text-shadow: 0 1px 0 @white;
    & + li:before {
      content: "/\00a0";
      padding: 0 5px;
      color: #ccc;
    }
  }
  & > .active {
    color: $grayLight;
  }
}



