@import '_hui.scss';
body,
html {
    height: 100%;
    width: 100%;
    min-width: 320px;
    -webkit-tap-highlight-color: transparent;
    overflow: hidden;
}


/* 覆盖mint-ui的primary颜色，改为自己UI的主题色 */

$color-primary: #05AFAF !important;
.mint-header {
    background-color: $color-primary;
}

.mint-button:not(.is-disabled):active::after {
    opacity: .2/* .6 */
}

.mint-button--primary {
    background-color: $color-primary;
}

.mint-button--primary.is-plain {
    border: 1px solid $color-primary;
    color: $color-primary
}

.mint-badge.is-primary {
    background-color: $color-primary
}

.mint-switch-input:checked+.mint-switch-core {
    border-color: $color-primary;
    background-color: $color-primary;
}

.mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid $color-primary;
    color: $color-primary;
}

.mint-tabbar>.mint-tab-item.is-selected {
    color: $color-primary;
}

.mint-searchbar-cancel {
    color: $color-primary;
}

.mint-checkbox-input:checked+.mint-checkbox-core {
    background-color: $color-primary;
    border-color: $color-primary;
}

.mint-radio-input:checked+.mint-radio-core {
    background-color: $color-primary;
    border-color: $color-primary;
}

.mt-range-progress {
    background-color: $color-primary;
}

.mt-progress-progress {
    background-color: $color-primary;
}

.mint-msgbox-confirm {
    color: $color-primary;
}

.mint-msgbox-confirm:active {
    color: $color-primary;
}

.mint-datetime-action {
    color: $color-primary;
}

#app,
.container {
    height: 100%;
    overflow: hidden;
}

// 全局默认样式
#app {
    height: 100%;
    overflow: hidden;
    max-width: 768px;
    margin: 0 auto;
    position: relative;
    color: #36465d;
    background-color: #fbf9fe;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.5);
}

.mint-tab-container {
    overflow: auto;
}

#app .home .home-wrap {
    padding-bottom: 60px;
}

.mint-tabbar.is-fixed {
    position: absolute;
    height: 60px;
}

.mint-tab-item-icon {
    height: 30px;
    line-height: 30px;
}

*,
 :after,
 :before {
    box-sizing: border-box;
}

.page {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 60px;
}

.panel {
    margin: 0 8px 12px;
    box-shadow: 0 4px 12px 0 hsla(0, 0%, 62%, 0.3);
    background: #fff;
    border-radius: 8px;
    padding: 12px;
    position: relative;
}

.head-title {
    position: relative;
    height: 60px;
    line-height: 60px;
    .head-right-icon {
        height: 100%;
        line-height: 1;
        .mint-button {
            margin-top: 10%;
        }
    }
}

.head-title h1 {
    font-size: 22px;
    font-weight: 500;
}


